// -----------------------------------------------------------------------------
// ç•«å¸ƒå°ºå¯¸åŒæ­¥
// -----------------------------------------------------------------------------
export function syncCanvasSize() {
  const stageEl = outCanvas.closest(".stage") || outCanvas.parentElement;

  // 1) å–å¯è¦–å€å°ºå¯¸ï¼ˆè¡Œå‹•è£ç½®åœ°å€åˆ—æ”¶åˆæ›´æº–ï¼‰
  const getViewportSize = () => {
    const vv = window.visualViewport;
    return {
      w: Math.round(vv?.width  || window.innerWidth  || document.documentElement.clientWidth  || 360),
      h: Math.round(vv?.height || window.innerHeight || document.documentElement.clientHeight || 640),
    };
  };

  // 2) CSS å°ºå¯¸ï¼ˆé‚è¼¯åƒç´ ï¼‰ï¼šæ»¿ç‰ˆå°±ç›´æ¥åƒ viewportï¼›å¦å‰‡åƒå®¹å™¨ rect
  let cssW, cssH;
  if (CONFIG.DISPLAY?.FULLBLEED) {
    const { w, h } = getViewportSize();
    cssW = w;
    cssH = h;

    // è®“å®¹å™¨æ’æ»¿ï¼ˆè‹¥ä½ å·²ç”¨ fixed inset-0ï¼Œå¯ä¿ç•™é€™æ®µæˆ–ç§»é™¤éƒ½è¡Œï¼‰
    if (stageEl && stageEl.style) {
      stageEl.style.position = "fixed";
      stageEl.style.top = "0";
      stageEl.style.left = "0";
      stageEl.style.right = "0";
      stageEl.style.bottom = "0";
      stageEl.style.width = "100dvw";
      stageEl.style.height = "100dvh";
      stageEl.style.margin = "0";
      stageEl.style.padding = "0";
      stageEl.style.overflow = "hidden";
    }
  } else {
    const rect = stageEl.getBoundingClientRect();
    cssW = Math.max(320, Math.round(rect.width));
    cssH = Math.max(240, Math.round(rect.height));
  }

  // â— ä¸è¦å†é™åˆ¶ CSS é«˜åº¦åˆ° 720ï¼Œè®“ç•«é¢çœŸçš„æ»¿ç‰ˆ
  // ï¼ˆä¿æ•ˆèƒ½æ”¹åœ¨åƒç´ å±¤åšï¼Œè€Œä¸æ˜¯ CSS å°ºå¯¸ï¼‰

  // 3) åƒç´ å±¤ç´šçš„è»Ÿä¸Šé™ï¼šé™åˆ¶ã€Œæœ‰æ•ˆ DPRã€è€Œé CSS å°ºå¯¸
  const dpr = Math.max(1, Math.min(window.devicePixelRatio || 1, 3));
  const maxWpx = (window.innerWidth > 768) ? 1920 : 1280;  // ä½ å¯ä¾æ©Ÿå‹å¾®èª¿
  const maxHpx = (window.innerHeight > 1024) ? 1080 : 1280;

  // è®“ canvas çœŸæ­£åƒç´ ä¸è¶…éä¸Šé™ï¼Œä½†ç•«é¢ä»ç¶­æŒæ»¿ç‰ˆ
  const dprEff = Math.min(dpr, maxWpx / cssW, maxHpx / cssH);

  const pixelW = Math.round(cssW * dprEff);
  const pixelH = Math.round(cssH * dprEff);

  const setSize = (c) => {
    if (c.width !== pixelW || c.height !== pixelH) {
      c.width  = pixelW;   // å¯¦éš›åƒç´ ï¼ˆæ±ºå®šæ¸…æ™°åº¦ï¼‰
      c.height = pixelH;
    }
    c.style.width  = `${cssW}px`; // CSS å°ºå¯¸ï¼ˆæ±ºå®šç•«é¢æ˜¯å¦æ»¿ç‰ˆï¼‰
    c.style.height = `${cssH}px`;
  };

  [bgCanvas, maskCanvas, outCanvas, videoDrawCanvas, compositeCanvas].forEach(setSize);

  lastMaskSize = { w: outCanvas.width, h: outCanvas.height };
}

