// æ•ç²åŸå§‹ç›¸æ©Ÿç•«é¢ï¼ˆç„¡èƒŒæ™¯æ›¿æ›ï¼‰
export async function captureOriginalFrame() {
  if (!videoEl || !videoEl.videoWidth || !videoEl.videoHeight) {
    console.error('Video element not ready for capture');
    return null;
  }
  
  // å‰µå»ºè‡¨æ™‚canvasä¾†æ•ç²åŸå§‹ç•«é¢
  const tempCanvas = document.createElement('canvas');
  tempCanvas.width = videoEl.videoWidth;
  tempCanvas.height = videoEl.videoHeight;
  const tempCtx = tempCanvas.getContext('2d');
  
  // ç¹ªè£½åŸå§‹ç›¸æ©Ÿç•«é¢
  tempCtx.drawImage(videoEl, 0, 0);
  
  // è¿”å› data URL
  return tempCanvas.toDataURL('image/png');
}

// ç”Ÿæˆå¸¶èƒŒæ™¯çš„é è¦½åœ–ç‰‡
export async function generatePreviewWithBackground(previewCanvas, originalImageData, backgroundImageSrc) {
  if (!previewCanvas || !originalImageData) {
    console.error('Missing canvas or image data for preview');
    return;
  }
  
  try {
    // è¼‰å…¥åŸå§‹åœ–ç‰‡
    const originalImg = new Image();
    originalImg.src = originalImageData;
    await new Promise((resolve, reject) => {
      originalImg.onload = resolve;
      originalImg.onerror = reject;
    });
    
    // è¼‰å…¥èƒŒæ™¯åœ–ç‰‡
    const backgroundImg = new Image();
    backgroundImg.src = backgroundImageSrc;
    await new Promise((resolve, reject) => {
      backgroundImg.onload = resolve;
      backgroundImg.onerror = reject;
    });
    
    // è¨­å®šcanvaså°ºå¯¸
    previewCanvas.width = originalImg.width;
    previewCanvas.height = originalImg.height;
    const ctx = previewCanvas.getContext('2d');
    
    // å¦‚æœæ²’æœ‰å¯ç”¨çš„äººåƒåˆ†å‰²è’™ç‰ˆï¼Œæš«æ™‚åªé¡¯ç¤ºåŸåœ–
    if (!lastMaskF32 || !mediaPipeManager.isInitialized()) {
      // å…ˆç¹ªè£½èƒŒæ™¯ï¼ˆç¸®æ”¾è‡³å¡«æ»¿ï¼‰
      ctx.drawImage(backgroundImg, 0, 0, previewCanvas.width, previewCanvas.height);
      
      // ç„¶å¾Œç¹ªè£½åŸåœ–ï¼ˆé€æ˜åº¦50%ä»¥å±•ç¤ºæ•ˆæœï¼‰
      ctx.globalAlpha = 0.8;
      ctx.drawImage(originalImg, 0, 0);
      ctx.globalAlpha = 1.0;
      return;
    }
    
    // ä½¿ç”¨ MediaPipe é€²è¡Œå¯¦éš›çš„èƒŒæ™¯æ›¿æ›
    // å…ˆç¹ªè£½èƒŒæ™¯
    ctx.drawImage(backgroundImg, 0, 0, previewCanvas.width, previewCanvas.height);
    
    // å‰µå»ºè’™ç‰ˆcanvas
    const maskCanvas = document.createElement('canvas');
    maskCanvas.width = previewCanvas.width;
    maskCanvas.height = previewCanvas.height;
    const maskCtx = maskCanvas.getContext('2d');
    
    // æ‡‰ç”¨åˆ†å‰²è’™ç‰ˆï¼ˆé€™è£¡éœ€è¦å°‡ lastMaskF32 è½‰æ›ç‚ºcanvasè’™ç‰ˆï¼‰
    if (lastMaskF32 && lastMaskSize.w > 0 && lastMaskSize.h > 0) {
      // å°‡Float32Arrayè’™ç‰ˆè½‰æ›ç‚ºImageData
      const imageData = maskCtx.createImageData(lastMaskSize.w, lastMaskSize.h);
      for (let i = 0; i < lastMaskF32.length; i++) {
        const maskValue = Math.floor(lastMaskF32[i] * 255);
        const pixelIndex = i * 4;
        imageData.data[pixelIndex] = maskValue;     // R
        imageData.data[pixelIndex + 1] = maskValue; // G  
        imageData.data[pixelIndex + 2] = maskValue; // B
        imageData.data[pixelIndex + 3] = 255;       // A
      }
      
      // å°‡è’™ç‰ˆç¹ªè£½åˆ°è‡¨æ™‚canvasä¸¦ç¸®æ”¾åˆ°ç›®æ¨™å°ºå¯¸
      maskCtx.putImageData(imageData, 0, 0);
      
      // ä½¿ç”¨è’™ç‰ˆåˆæˆæœ€çµ‚åœ–åƒ
      ctx.globalCompositeOperation = 'destination-in';
      ctx.drawImage(maskCanvas, 0, 0, previewCanvas.width, previewCanvas.height);
      
      // ç¹ªè£½å‰æ™¯ï¼ˆäººåƒï¼‰
      ctx.globalCompositeOperation = 'source-over';
      ctx.drawImage(originalImg, 0, 0);
      
      // é‡ç½®åˆæˆæ¨¡å¼
      ctx.globalCompositeOperation = 'source-over';
    }
    
  } catch (error) {
    console.error('Preview generation failed:', error);
    // éŒ¯èª¤æ™‚é¡¯ç¤ºåŸåœ–
    const ctx = previewCanvas.getContext('2d');
    const img = new Image();
    img.src = originalImageData;
    img.onload = () => {
      previewCanvas.width = img.width;
      previewCanvas.height = img.height;
      ctx.drawImage(img, 0, 0);
    };
  }
}

// ç‚ºé è¦½é é¢ç”Ÿæˆäººåƒè’™ç‰ˆ
export async function generatePersonMask(canvas, originalImg) {
  if (!canvas || !originalImg) {
    console.error('Missing canvas or original image for person mask generation');
    return;
  }
  
  try {
    const ctx = canvas.getContext('2d');
    
    // å¦‚æœ MediaPipe æ²’æœ‰åˆå§‹åŒ–æˆ–æ²’æœ‰å¯ç”¨çš„è’™ç‰ˆæ•¸æ“šï¼Œé¡¯ç¤ºåŸåœ–
    if (!mediaPipeManager.isInitialized() || !lastMaskF32 || !lastMaskSize.w || !lastMaskSize.h) {
      console.warn('MediaPipe not initialized or no mask available, showing original image');
      
      // ä»¥é¡åƒæ–¹å¼ç¹ªè£½åŸåœ–ï¼ˆèˆ‡ç›¸æ©Ÿé è¦½ä¸€è‡´ï¼‰
      ctx.save();
      ctx.scale(-1, 1);
      ctx.translate(-canvas.width, 0);
      ctx.drawImage(originalImg, 0, 0, canvas.width, canvas.height);
      ctx.restore();
      return;
    }
    
    // å‰µå»ºè‡¨æ™‚ canvas ä¾†è™•ç†è’™ç‰ˆ
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = canvas.width;
    tempCanvas.height = canvas.height;
    const tempCtx = tempCanvas.getContext('2d');
    
    // ä»¥é¡åƒæ–¹å¼ç¹ªè£½åŸåœ–åˆ°è‡¨æ™‚ canvas
    tempCtx.save();
    tempCtx.scale(-1, 1);
    tempCtx.translate(-tempCanvas.width, 0);
    tempCtx.drawImage(originalImg, 0, 0, tempCanvas.width, tempCanvas.height);
    tempCtx.restore();
    
    // å‰µå»ºè’™ç‰ˆ canvas
    const maskCanvas = document.createElement('canvas');
    maskCanvas.width = lastMaskSize.w;
    maskCanvas.height = lastMaskSize.h;
    const maskCtx = maskCanvas.getContext('2d');
    
    // å°‡ Float32Array è’™ç‰ˆè½‰æ›ç‚º ImageData
    const maskImageData = maskCtx.createImageData(lastMaskSize.w, lastMaskSize.h);
    const threshold = CONFIG.SMOOTHING.THRESHOLD || 0.5;
    
    for (let i = 0; i < lastMaskF32.length; i++) {
      const maskValue = lastMaskF32[i];
      const alpha = maskValue >= threshold ? Math.min(255, Math.round(maskValue * 255)) : 0;
      const pixelIndex = i * 4;
      
      maskImageData.data[pixelIndex] = 255;     // R
      maskImageData.data[pixelIndex + 1] = 255; // G
      maskImageData.data[pixelIndex + 2] = 255; // B
      maskImageData.data[pixelIndex + 3] = alpha; // A
    }
    
    // å°‡è’™ç‰ˆç¹ªè£½åˆ°è’™ç‰ˆ canvas
    maskCtx.putImageData(maskImageData, 0, 0);
    
    // å°‡è’™ç‰ˆç¸®æ”¾åˆ°è¼¸å‡ºå°ºå¯¸
    const scaledMaskCanvas = document.createElement('canvas');
    scaledMaskCanvas.width = canvas.width;
    scaledMaskCanvas.height = canvas.height;
    const scaledMaskCtx = scaledMaskCanvas.getContext('2d');
    scaledMaskCtx.drawImage(maskCanvas, 0, 0, canvas.width, canvas.height);
    
    // ä½¿ç”¨è’™ç‰ˆåˆæˆæœ€çµ‚åœ–åƒ
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(tempCanvas, 0, 0);
    
    // æ‡‰ç”¨è’™ç‰ˆ
    ctx.globalCompositeOperation = 'destination-in';
    ctx.drawImage(scaledMaskCanvas, 0, 0);
    
    // é‡ç½®åˆæˆæ¨¡å¼
    ctx.globalCompositeOperation = 'source-over';
    
  } catch (error) {
    console.error('Person mask generation failed:', error);
    
    // éŒ¯èª¤æ™‚é¡¯ç¤ºåŸåœ–
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.save();
    ctx.scale(-1, 1);
    ctx.translate(-canvas.width, 0);
    ctx.drawImage(originalImg, 0, 0, canvas.width, canvas.height);
    ctx.restore();
  }
}

// é è¦½æ¨¡å¼è®Šæ•¸
let previewMode = false;
let previewImageData = null;
let previewBackgroundSrc = null;

// é€²å…¥é è¦½æ¨¡å¼
export function enterPreviewMode(imageDataUrl, backgroundSrc) {
  previewMode = true;
  previewImageData = imageDataUrl;
  previewBackgroundSrc = backgroundSrc;
  
  // å•Ÿç”¨åˆ†å‰²
  setEnableSegmentation(true);
  
  // è¨­ç½®è‡ªå®šç¾©èƒŒæ™¯
  setBackgroundType('custom_bg');
  if (backgroundSrc) {
    backgroundRenderer.setCustomBackground(backgroundSrc);
  }
}

// é›¢é–‹é è¦½æ¨¡å¼
export function exitPreviewMode() {
  previewMode = false;
  previewImageData = null;
  previewBackgroundSrc = null;
  
  // æ¢å¾©ç›¸æ©Ÿæ¨¡å¼
  setEnableSegmentation(false);
  setBackgroundType('custom_moon_bg');
}

// åˆ‡æ›é è¦½èƒŒæ™¯
export function switchPreviewBackground(backgroundSrc) {
  if (!previewMode) return;
  
  previewBackgroundSrc = backgroundSrc;
  backgroundRenderer.setCustomBackground(backgroundSrc);
}

// èˆŠçš„éŒ„å½±å‡½æ•¸å·²ç§»é™¤ï¼Œç¾åœ¨ä½¿ç”¨ recordingManager