import { CONFIG } from './config.js';

export class BackgroundRenderer {
  constructor() {
    this.animT = 0;
    this.customBackgroundImage = null;
    this.isIOSDevice = false;
    this.loadCustomBackground();
  }

  // è¨­å®š iOS è¨­å‚™æ¨™è­˜
  setIOSDevice(isIOS) {
    this.isIOSDevice = isIOS;
  }

  // è¼‰å…¥è‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡
  loadCustomBackground() {
    const img = new Image();
    img.onload = () => {
      this.customBackgroundImage = img;
      console.log('âœ… è‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡è¼‰å…¥å®Œæˆ');
    };
    img.onerror = (e) => {
      console.warn('âš ï¸ è‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡è¼‰å…¥å¤±æ•—:', e);
    };
    
    const base = import.meta.env.BASE_URL || '/';
    img.src = base + 'pexels-photo-623218.jpeg';
  }

  // è¨­å®šè‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡
  setCustomBackground(imageSrc) {
    const img = new Image();
    img.onload = () => {
      this.customBackgroundImage = img;
      console.log('âœ… èƒŒæ™¯åœ–ç‰‡å·²æ›´æ–°:', imageSrc);
    };
    img.onerror = (e) => {
      console.warn('âš ï¸ èƒŒæ™¯åœ–ç‰‡è¼‰å…¥å¤±æ•—:', e);
    };
    img.src = imageSrc;
  }

  // æ›´æ–°å‹•ç•«æ™‚é–“
  updateAnimation() {
    this.animT += 0.016;
  }

  // æ¸²æŸ“èƒŒæ™¯åˆ°æŒ‡å®š Canvas Context
  renderBackgroundTo(ctx, w, h, bgVideoEl, bgMoonVideoEl, iosMoonVideoEl) {
    const bgType =
      (CONFIG.BACKGROUND && CONFIG.BACKGROUND.TYPE) ||
      (CONFIG.BACKGROUND_EFFECTS &&
        CONFIG.BACKGROUND_EFFECTS.DEFAULT_BACKGROUND) ||
      "original";
    const intensity =
      CONFIG.BACKGROUND && typeof CONFIG.BACKGROUND.INTENSITY === "number"
        ? CONFIG.BACKGROUND.INTENSITY
        : (CONFIG.BACKGROUND_EFFECTS &&
            CONFIG.BACKGROUND_EFFECTS.EFFECT_INTENSITY) ??
          0.5;

    ctx.clearRect(0, 0, w, h);
    this.updateAnimation();

    // åŸå§‹é€æ˜èƒŒæ™¯
    if (bgType === "original") {
      return;
    }

    // æ³¢æµªæ•ˆæœ
    if (bgType === "waves") {
      this.renderWaveBackground(ctx, w, h, intensity);
      return;
    }

    // é »è­œæ•ˆæœ
    if (bgType === "frequency") {
      this.renderFrequencyBackground(ctx, w, h, intensity);
      return;
    }

    // èƒŒæ™¯å½±ç‰‡
    if (bgType === "video" && bgVideoEl) {
      this.renderVideoBackground(ctx, w, h, bgVideoEl, intensity);
      return;
    }

    // æœˆç›¸å½±ç‰‡èƒŒæ™¯
    if (bgType === "moon_video" && bgMoonVideoEl) {
      this.renderMoonVideoBackground(ctx, w, h, bgMoonVideoEl, intensity);
      return;
    }
    
    // è‡ªå®šç¾©èƒŒæ™¯ + åœ“å½¢æœˆäº®åˆæˆ
    if (bgType === "custom_moon_bg" && this.customBackgroundImage) {
      this.renderCustomMoonBackground(ctx, w, h, bgMoonVideoEl, iosMoonVideoEl, intensity);
      return;
    }
  }

  // æ³¢æµªèƒŒæ™¯æ•ˆæœ
  renderWaveBackground(ctx, w, h, intensity) {
    const grad = ctx.createLinearGradient(0, 0, w, h);
    grad.addColorStop(0, "#1a2332");
    grad.addColorStop(0.5, "#2d3a50");
    grad.addColorStop(1, "#0f1419");
    ctx.fillStyle = grad;
    ctx.fillRect(0, 0, w, h);
    ctx.strokeStyle = `rgba(100,200,255,${intensity * 0.6})`;
    ctx.lineWidth = 2;
    
    for (let i = 0; i < 8; i++) {
      ctx.beginPath();
      for (let x = 0; x < w; x += 5) {
        const off = i * 50;
        const y =
          h / 2 + Math.sin((x + off) * 0.012 + this.animT * 1.2) * 20 * intensity;
        if (x === 0) ctx.moveTo(x, y);
        else ctx.lineTo(x, y);
      }
      ctx.stroke();
    }
  }

  // é »è­œèƒŒæ™¯æ•ˆæœ
  renderFrequencyBackground(ctx, w, h, intensity) {
    ctx.fillStyle = "#0a0a0a";
    ctx.fillRect(0, 0, w, h);
    const bands = 48;
    const bw = w / bands;
    
    for (let i = 0; i < bands; i++) {
      const x = i * bw;
      const bh =
        (Math.sin(this.animT * 2 + i * 0.2) * 0.5 + 0.5) * h * 0.5 * intensity;
      const hue = (this.animT * 30 + i * 10) % 360;
      ctx.fillStyle = `hsla(${hue},70%,60%,0.8)`;
      ctx.fillRect(x, h - bh, bw - 1, bh);
    }
  }

  // èƒŒæ™¯å½±ç‰‡æ¸²æŸ“
  renderVideoBackground(ctx, w, h, bgVideoEl, intensity) {
    const vw = bgVideoEl.videoWidth || 1;
    const vh = bgVideoEl.videoHeight || 1;
    
    if (bgVideoEl.readyState >= 2) {
      const scale = Math.max(w / vw, h / vh);
      const dw = Math.round(vw * scale);
      const dh = Math.round(vh * scale);
      const ox = Math.round((w - dw) / 2);
      const oy = Math.round((h - dh) / 2);
      
      try {
        if (bgVideoEl.paused) bgVideoEl.play().catch(() => {});
      } catch {}
      
      ctx.globalAlpha = Math.min(1, 0.3 + intensity * 0.7);
      ctx.drawImage(bgVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
      ctx.globalAlpha = 1;
    }
  }

  // æœˆç›¸å½±ç‰‡èƒŒæ™¯æ¸²æŸ“
  renderMoonVideoBackground(ctx, w, h, bgMoonVideoEl, intensity) {
    try {
      if (bgMoonVideoEl.readyState >= 2 && !bgMoonVideoEl.paused) {
        const vw = bgMoonVideoEl.videoWidth || bgMoonVideoEl.width || 1;
        const vh = bgMoonVideoEl.videoHeight || bgMoonVideoEl.height || 1;
        
        if (vw > 1 && vh > 1) {
          const scale = Math.max(w / vw, h / vh);
          const dw = Math.round(vw * scale);
          const dh = Math.round(vh * scale);
          const ox = Math.round((w - dw) / 2);
          const oy = Math.round((h - dh) / 2);

          ctx.globalAlpha = Math.min(1, 0.3 + intensity * 0.7);
          ctx.drawImage(bgMoonVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
          ctx.globalAlpha = 1;
        }
      } else if (bgMoonVideoEl.paused) {
        bgMoonVideoEl.play().catch((e) => console.warn("æœˆç›¸å½±ç‰‡æ’­æ”¾å¤±æ•—:", e));
      }
    } catch (e) {
      console.warn("æœˆç›¸å½±ç‰‡æ¸²æŸ“éŒ¯èª¤:", e);
    }
  }

  // è‡ªå®šç¾©èƒŒæ™¯ + åœ“å½¢æœˆäº®åˆæˆæ¸²æŸ“
  renderCustomMoonBackground(ctx, w, h, bgMoonVideoEl, iosMoonVideoEl, intensity) {
    try {
      // iOS è¨­å‚™ç‰¹æ®Šè™•ç†ï¼šä½¿ç”¨èƒŒæ™¯åœ–ç‰‡ + App.vue ä¸­çš„å…¨è¢å¹•é€æ˜ MOV
      if (this.isIOSDevice) {
        if (this.customBackgroundImage) {
          const imgW = this.customBackgroundImage.naturalWidth || this.customBackgroundImage.width;
          const imgH = this.customBackgroundImage.naturalHeight || this.customBackgroundImage.height;
          
          if (imgW > 0 && imgH > 0) {
            const scale = Math.max(w / imgW, h / imgH);
            const dw = Math.round(imgW * scale);
            const dh = Math.round(imgH * scale);
            const ox = Math.round((w - dw) / 2);
            const oy = Math.round((h - dh) / 2);
            ctx.drawImage(this.customBackgroundImage, 0, 0, imgW, imgH, ox, oy, dw, dh);
          }
        }
        return;
      }
      
      // é iOS è¨­å‚™ï¼šå‚³çµ±ä¸‰å±¤åˆæˆæ¨¡å¼
      // ç¬¬ä¸€å±¤ï¼šè‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡
      if (this.customBackgroundImage) {
        const imgW = this.customBackgroundImage.naturalWidth || this.customBackgroundImage.width;
        const imgH = this.customBackgroundImage.naturalHeight || this.customBackgroundImage.height;
        
        if (imgW > 0 && imgH > 0) {
          const scale = Math.max(w / imgW, h / imgH);
          const dw = Math.round(imgW * scale);
          const dh = Math.round(imgH * scale);
          const ox = Math.round((w - dw) / 2);
          const oy = Math.round((h - dh) / 2);
          ctx.drawImage(this.customBackgroundImage, 0, 0, imgW, imgH, ox, oy, dw, dh);
        }
      }
      
      // ç¬¬äºŒå±¤ï¼šåœ“å½¢æœˆäº® MP4 (é iOS è¨­å‚™ç”¨)
      if (bgMoonVideoEl && bgMoonVideoEl.readyState >= 2 && !bgMoonVideoEl.paused) {
        this.renderCircularMoon(ctx, w, h, bgMoonVideoEl, intensity);
      } else if (bgMoonVideoEl && bgMoonVideoEl.paused) {
        bgMoonVideoEl.play().catch((e) => console.warn("æœˆç›¸å½±ç‰‡æ’­æ”¾å¤±æ•—:", e));
      }
      
    } catch (e) {
      console.warn("è‡ªå®šç¾©èƒŒæ™¯ + åœ“å½¢æœˆäº®æ¸²æŸ“éŒ¯èª¤:", e);
    }
  }

  // åœ“å½¢æœˆäº®æ¸²æŸ“
  renderCircularMoon(ctx, w, h, bgMoonVideoEl, intensity) {
    const vw = bgMoonVideoEl.videoWidth || bgMoonVideoEl.width || 1;
    const vh = bgMoonVideoEl.videoHeight || bgMoonVideoEl.height || 1;
    
    if (vw > 1 && vh > 1) {
      ctx.save();
      
      // éŸ¿æ‡‰å¼åœ“å½¢è£å‰ªè·¯å¾‘è¨ˆç®—
      const aspectRatio = w / h;
      let centerX, centerY, radius;
      
      if (aspectRatio > 0.6) {
        centerX = w * 0.5;
        centerY = h * 0.35;
        radius = Math.min(w, h) * 0.25;
      } else if (aspectRatio > 0.5) {
        centerX = w * 0.51;
        centerY = h * 0.29;
        radius = Math.min(w, h) * 0.29;
      } else {
        centerX = w * 0.51;
        centerY = h * 0.26;
        radius = Math.min(w, h) * 0.30;
      }
      
      ctx.beginPath();
      ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
      ctx.clip();
      
      const scale = Math.max(w / vw, h / vh);
      const dw = Math.round(vw * scale);
      const dh = Math.round(vh * scale);
      const ox = Math.round((w - dw) / 2);
      const oy = Math.round((h - dh) / 2);
      
      ctx.globalAlpha = Math.min(1, 0.8 + intensity * 0.2);
      ctx.drawImage(bgMoonVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
      ctx.globalAlpha = 1;
      
      ctx.restore();
    }
  }
}