// recordingManager.js - éŒ„å½±å’Œæ‹ç…§ç®¡ç†
// è² è²¬ï¼šéŒ„å½±å­˜å„²ã€åˆ†äº«ã€æ‹ç…§åŠŸèƒ½ã€æª”æ¡ˆç®¡ç†

// å„²å­˜æœ€å¾Œä¸€æ¬¡éŒ„è£½çš„ Blob / filename / objectURL
let lastRecordedBlob = null;
let lastRecordedFilename = null;
let lastRecordedObjectUrl = null;
let recordingProgressCallback = null;
let videoOutputFormat = "mp4";

function _revokeLastObjectUrl() {
  try {
    if (lastRecordedObjectUrl) {
      URL.revokeObjectURL(lastRecordedObjectUrl);
      lastRecordedObjectUrl = null;
    }
  } catch (e) {
    console.warn("revoke failed", e);
  }
}

export class RecordingManager {
  constructor() {
    this.progressCallback = null;
    this.outputFormat = "mp4";
  }

  // è¨­å®šéŒ„è£½é€²åº¦å›èª¿
  setProgressCallback(callback) {
    recordingProgressCallback = callback;
    this.progressCallback = callback;
  }

  // è¨­å®šå½±ç‰‡è¼¸å‡ºæ ¼å¼
  setOutputFormat(format) {
    videoOutputFormat = format;
    this.outputFormat = format;
  }

  // å„²å­˜éŒ„å½± Blob ä¸¦å»ºç«‹ objectURL
  storeRecordingBlob(blob, filename) {
    if (!blob) return null;
    
    _revokeLastObjectUrl();
    lastRecordedBlob = blob;
    lastRecordedFilename = filename || `moonAR_${Date.now()}.webm`;
    
    try {
      lastRecordedObjectUrl = URL.createObjectURL(blob);
    } catch (e) {
      console.warn("createObjectURL failed", e);
      lastRecordedObjectUrl = null;
    }
    return lastRecordedObjectUrl;
  }

  // æª¢æŸ¥æ˜¯å¦æœ‰éŒ„å½±æª”å¯ç”¨
  hasRecordingAvailable() {
    return !!lastRecordedBlob;
  }

  // å–å¾—æœ€å¾ŒéŒ„å½±çš„ URL
  getLastRecordingUrl() {
    return lastRecordedObjectUrl || null;
  }

  // æ¸…é™¤æœ€å¾Œä¸€æ¬¡éŒ„å½±çš„æš«å­˜
  clearLastRecording() {
    _revokeLastObjectUrl();
    lastRecordedBlob = null;
    lastRecordedFilename = null;
  }

  // å–å¾—æœ€å¾ŒéŒ„å½±æª”å
  getLastRecordingFilename() {
    return lastRecordedFilename;
  }

  // ä¸Šå‚³åˆ°ä¼ºæœå™¨ä¸¦å–å¾— URL
  async uploadBlobToServerAndGetUrl(blob, filename) {
    const fd = new FormData();
    fd.append("file", blob, filename);
    const res = await fetch("/api/upload", { method: "POST", body: fd });
    if (!res.ok) throw new Error("upload failed");
    const j = await res.json();
    if (!j?.url) throw new Error("upload response missing url");
    return j.url;
  }

  // åˆ†äº«æœ€å¾ŒéŒ„å½±
  async shareLastRecording() {
    if (!lastRecordedBlob) throw new Error("no recording available");
    const filename = lastRecordedFilename || `moonAR_${Date.now()}.mp4`;
    const file = new File([lastRecordedBlob], filename, {
      type: lastRecordedBlob.type || "video/mp4",
    });

    // å„ªå…ˆå˜—è©¦æª”æ¡ˆåˆ†äº«
    try {
      if (navigator.canShare && navigator.canShare({ files: [file] })) {
        await navigator.share({
          files: [file],
          title: "æœˆäº®ç¯€ AR å½±ç‰‡",
          text: "åˆ†äº«çµ¦ä½ ",
        });
        return { ok: true, method: "webshare" };
      }
    } catch (err) {
      console.warn("Web Share file error:", err);
    }

    // å˜—è©¦ä¸Šå‚³ä¸¦åˆ†äº« URL
    try {
      const url = await this.uploadBlobToServerAndGetUrl(lastRecordedBlob, filename);
      if (navigator.share) {
        try {
          await navigator.share({
            title: "æœˆäº®ç¯€ AR å½±ç‰‡",
            text: "çœ‹é€™å€‹å½±ç‰‡ï¼š",
            url,
          });
          return { ok: true, method: "url-share", url };
        } catch (err) {
          console.warn("Web Share URL error:", err);
        }
      }
      return { ok: true, method: "uploaded", url };
    } catch (err) {
      console.warn("upload failed:", err);
      throw err;
    }
  }

  // æ‹ç…§åŠŸèƒ½
  async capturePhoto(outCanvas, bgCanvas, CONFIG, backgroundRenderer, bgMoonVideoEl) {
    if (!outCanvas || !backgroundRenderer) {
      console.warn("ç„¡æ³•æ‹ç…§ï¼šç›¸æ©Ÿæœªå•Ÿå‹•æˆ–ç•«å¸ƒä¸å­˜åœ¨");
      return null;
    }

    try {
      const captureCanvas = document.createElement("canvas");
      const captureCtx = captureCanvas.getContext("2d");
      const W = outCanvas.width;
      const H = outCanvas.height;
      captureCanvas.width = W;
      captureCanvas.height = H;

      const bgType = CONFIG.BACKGROUND && CONFIG.BACKGROUND.TYPE;
      console.log("ğŸ“¸ æ‹ç…§æª¢æŸ¥èƒŒæ™¯é¡å‹:", { bgType, hasMoonVideo: !!bgMoonVideoEl });

      if ((bgType === "moon_video" || bgType === "custom_moon_bg") && bgMoonVideoEl) {
        // æœˆç›¸å½±ç‰‡æ¨¡å¼ï¼šç”Ÿæˆå‹•æ…‹å½±ç‰‡
        console.log("ğŸŒ™ æœˆç›¸å½±ç‰‡æ¨¡å¼ï¼šç”Ÿæˆ 8 ç§’å‹•æ…‹å½±ç‰‡...");
        captureCtx.drawImage(outCanvas, 0, 0, W, H);
        const staticPersonDataURL = captureCanvas.toDataURL("image/png", 1.0);

        try {
          const result = await this.generateVideoWithStaticPerson(
            staticPersonDataURL, 
            W, 
            H, 
            CONFIG, 
            backgroundRenderer, 
            bgMoonVideoEl
          );
          console.log("âœ… å½±ç‰‡ç”Ÿæˆå®Œæˆ:", result);
          return result;
        } catch (error) {
          console.error("âŒ å½±ç‰‡ç”Ÿæˆå¤±æ•—:", error);
          // å›é€€åˆ° PNG
          return this.savePNG(captureCanvas);
        }
      } else {
        // ä¸€èˆ¬æ¨¡å¼
        captureCtx.drawImage(bgCanvas, 0, 0, W, H);
        captureCtx.drawImage(outCanvas, 0, 0, W, H);
      }

      return this.savePNG(captureCanvas);
    } catch (error) {
      console.error("æ‹ç…§å¤±æ•—ï¼š", error);
      return null;
    }
  }

  // å„²å­˜ PNG åœ–ç‰‡
  savePNG(canvas) {
    const dataURL = canvas.toDataURL("image/png", 1.0);
    const timestamp = new Date()
      .toISOString()
      .replace(/[:]/g, "-")
      .split(".")[0];

    const pngLink = document.createElement("a");
    const pngFilename = `æœˆäº®ç¯€ARæ‹ç…§_${timestamp}.png`;
    pngLink.download = pngFilename;
    pngLink.href = dataURL;
    document.body.appendChild(pngLink);
    pngLink.click();
    document.body.removeChild(pngLink);

    console.log(`ğŸ“¸ PNG æ‹ç…§æˆåŠŸï¼`);
    return { png: dataURL };
  }

  // ç”Ÿæˆéœæ­¢äººç‰© + å‹•æ…‹å½±ç‰‡èƒŒæ™¯
  async generateVideoWithStaticPerson(staticPersonDataURL, width, height, CONFIG, backgroundRenderer, bgMoonVideoEl) {
    console.log("ğŸ¬ é–‹å§‹éŒ„è£½éœæ­¢äººç‰© + å‹•æ…‹èƒŒæ™¯å½±ç‰‡...");

    if (!window.MediaRecorder) {
      console.warn("ç€è¦½å™¨ä¸æ”¯æ´ MediaRecorder");
      return { png: staticPersonDataURL, error: "ç€è¦½å™¨ä¸æ”¯æ´å½±ç‰‡éŒ„è£½" };
    }

    const recordCanvas = document.createElement("canvas");
    const recordCtx = recordCanvas.getContext("2d");
    const maxSize = 640;
    const aspectRatio = width / height;

    if (width > height) {
      recordCanvas.width = Math.min(maxSize, width);
      recordCanvas.height = Math.round(recordCanvas.width / aspectRatio);
    } else {
      recordCanvas.height = Math.min(maxSize, height);
      recordCanvas.width = Math.round(recordCanvas.height * aspectRatio);
    }

    const personImg = new Image();

    return new Promise((resolve, reject) => {
      personImg.onload = async () => {
        const DURATION_SECONDS = 8;
        const FPS = 20;
        const totalFrames = DURATION_SECONDS * FPS;

        // ç¢ºä¿æœˆç›¸å½±ç‰‡é–‹å§‹æ’­æ”¾
        if (bgMoonVideoEl && bgMoonVideoEl.paused) {
          await bgMoonVideoEl.play().catch((e) => console.warn("å½±ç‰‡æ’­æ”¾å¤±æ•—:", e));
        }

        const stream = recordCanvas.captureStream(FPS);
        const recorderOptions = this.getRecorderOptions();
        const recorder = new MediaRecorder(stream, recorderOptions);
        
        const chunks = [];
        recorder.ondataavailable = (event) => {
          if (event.data.size > 0) chunks.push(event.data);
        };

        recorder.onstop = () => {
          const mimeType = this.outputFormat === "mp4" ? "video/mp4" : "video/webm";
          const extension = this.outputFormat === "mp4" ? "mp4" : "webm";
          const blob = new Blob(chunks, { type: mimeType });

          const timestamp = new Date()
            .toISOString()
            .replace(/[:]/g, "-")
            .split(".")[0];
          const filename = `æœˆäº®ç¯€AR_æœˆç›¸å½±ç‰‡_${timestamp}.${extension}`;

          const storedUrl = this.storeRecordingBlob(blob, filename);
          resolve({
            png: staticPersonDataURL,
            video: storedUrl,
            duration: DURATION_SECONDS,
            format: extension,
            saved: true,
          });
        };

        recorder.onerror = (event) => {
          console.error("éŒ„è£½éŒ¯èª¤:", event.error);
          reject(event.error);
        };

        recorder.start();
        this.recordFrames(recordCtx, recordCanvas, personImg, totalFrames, FPS, CONFIG, backgroundRenderer, bgMoonVideoEl);
      };

      personImg.onerror = () => reject(new Error("éœæ­¢äººç‰©åœ–ç‰‡è¼‰å…¥å¤±æ•—"));
      personImg.src = staticPersonDataURL;
    });
  }

  // å–å¾—éŒ„è£½å™¨é¸é …
  getRecorderOptions() {
    if (this.outputFormat === "mp4") {
      if (MediaRecorder.isTypeSupported("video/mp4; codecs=h264")) {
        return { mimeType: "video/mp4; codecs=h264", videoBitsPerSecond: 3000000 };
      } else if (MediaRecorder.isTypeSupported("video/mp4")) {
        return { mimeType: "video/mp4", videoBitsPerSecond: 3000000 };
      } else {
        console.warn("ç€è¦½å™¨ä¸æ”¯æ´ MP4ï¼Œé™ç´šåˆ° WebM");
        this.outputFormat = "webm";
        return { mimeType: "video/webm; codecs=vp8", videoBitsPerSecond: 3000000 };
      }
    } else {
      return { mimeType: "video/webm; codecs=vp8", videoBitsPerSecond: 3000000 };
    }
  }

  // éŒ„è£½å¹€
  recordFrames(recordCtx, recordCanvas, personImg, totalFrames, FPS, CONFIG, backgroundRenderer, bgMoonVideoEl) {
    let frame = 0;

    const drawFrame = () => {
      recordCtx.clearRect(0, 0, recordCanvas.width, recordCanvas.height);

      // æ¸²æŸ“èƒŒæ™¯
      backgroundRenderer.renderBackgroundTo(
        recordCtx,
        recordCanvas.width,
        recordCanvas.height,
        null, // bgVideoEl
        bgMoonVideoEl,
        null  // iosMoonVideoEl
      );

      // æ¸²æŸ“éœæ­¢äººç‰©
      const personWidth = personImg.naturalWidth || personImg.width;
      const personHeight = personImg.naturalHeight || personImg.height;

      if (personWidth > 0 && personHeight > 0) {
        const personScale = Math.max(
          recordCanvas.width / personWidth,
          recordCanvas.height / personHeight
        );
        const personDrawWidth = Math.round(personWidth * personScale);
        const personDrawHeight = Math.round(personHeight * personScale);
        const personOffsetX = Math.round((recordCanvas.width - personDrawWidth) / 2);
        const personOffsetY = Math.round((recordCanvas.height - personDrawHeight) / 2);

        recordCtx.drawImage(
          personImg,
          0, 0, personWidth, personHeight,
          personOffsetX, personOffsetY, personDrawWidth, personDrawHeight
        );
      }

      frame++;
      
      // æ›´æ–°é€²åº¦
      if (this.progressCallback) {
        const currentTime = frame / FPS;
        this.progressCallback.updateProgress(currentTime, 8);
      }

      // ç¹¼çºŒæˆ–çµæŸ
      if (frame < totalFrames) {
        setTimeout(drawFrame, 1000 / FPS);
      } else {
        if (this.progressCallback) {
          this.progressCallback.setStatus("éŒ„è£½å®Œæˆï¼Œæ­£åœ¨ç”Ÿæˆæª”æ¡ˆ...");
        }
        console.log("æ‰€æœ‰å¹€éŒ„è£½å®Œæˆ");
      }
    };

    drawFrame();
  }
}

// å‘å¾Œç›¸å®¹çš„å…¨åŸŸå‡½æ•¸å°å‡º
export function storeRecordingBlob(blob, filename) {
  const manager = new RecordingManager();
  return manager.storeRecordingBlob(blob, filename);
}

export function hasRecordingAvailable() {
  return !!lastRecordedBlob;
}

export function getLastRecordingUrl() {
  return lastRecordedObjectUrl || null;
}

export function clearLastRecording() {
  _revokeLastObjectUrl();
  lastRecordedBlob = null;
  lastRecordedFilename = null;
}

export function getLastRecordingFilename() {
  return lastRecordedFilename;
}

export async function uploadBlobToServerAndGetUrl(blob, filename) {
  const manager = new RecordingManager();
  return manager.uploadBlobToServerAndGetUrl(blob, filename);
}

export async function shareLastRecording() {
  const manager = new RecordingManager();
  return manager.shareLastRecording();
}

export function setRecordingProgressCallback(callback) {
  recordingProgressCallback = callback;
}

export function setVideoOutputFormat(format) {
  videoOutputFormat = format;
}