import { CONFIG } from './config.js';

export class CameraManager {
  constructor() {
    this.stream = null;
    this.currentFacingMode = CONFIG.CAMERA.DEFAULT_FACING_MODE || "user";
    this.videoEl = null;
  }

  // è¨­å®šå½±ç‰‡å…ƒç´ 
  setVideoElement(videoEl) {
    this.videoEl = videoEl;
  }

  // å•Ÿå‹•ç›¸æ©Ÿ
  async startCamera() {
    // é—œé–‰èˆŠä¸²æµ
    if (this.stream) {
      try { this.stream.getTracks().forEach((t) => t.stop()); } catch {}
    }
  
    // ä¾èˆå°ï¼ˆ.stageï¼‰æˆ–è¦–çª—å¤§å° + DPR æ¨ä¼°ç†æƒ³è§£æåº¦
    const stage = this.videoEl?.closest('.stage');
    const targetCssW = stage?.clientWidth  || window.innerWidth;
    const targetCssH = stage?.clientHeight || window.innerHeight;
    const dpr = Math.min(window.devicePixelRatio || 1, 3);
  
    // ä¸Šé™ä¿å®ˆï¼Œé¿å…æŸäº›æ‰‹æ©Ÿç›´æ¥æ‹’çµ•
    const idealW = Math.min(Math.round(targetCssW * dpr), 1920);
    const idealH = Math.min(Math.round(targetCssH * dpr), 1080);
    const aspect = idealW / Math.max(1, idealH);
  
    const constraints = {
      audio: false,
      video: {
        facingMode: this.currentFacingMode,            // "user" æˆ– "environment"
        width:        { ideal: idealW,  min: 640,  max: 2560 },
        height:       { ideal: idealH,  min: 480,  max: 1440 },
        aspectRatio:  { ideal: aspect },
        frameRate:    { ideal: CONFIG.CAMERA.FRAME_RATE || 30, max: 60 },
        // æŸäº› iOS æ©Ÿå‹æ”¯æ´ï¼šresizeMode: "none"
        // resizeMode: "none",
      },
    };
  
    this.stream = await navigator.mediaDevices.getUserMedia(constraints);
    this.videoEl.srcObject = this.stream;
    this.videoEl.setAttribute("playsinline", "");
    this.videoEl.muted = true;
    this.videoEl.autoplay = true;
  
    // ç­‰å¾… metadata æº–å‚™å¥½
    await new Promise((resolve) => {
      if (this.videoEl.readyState >= 1 && this.videoEl.videoWidth && this.videoEl.videoHeight) return resolve();
      this.videoEl.onloadedmetadata = () => resolve();
      setTimeout(resolve, 1500);
    });
  
    // äºŒæ¬¡å˜—è©¦ï¼šä¾èƒ½åŠ›ä¸Šé™ applyConstraintsï¼ˆè¨±å¤šç€è¦½å™¨ç¬¬ä¸€æ¬¡æœƒåªçµ¦ 640x480ï¼‰
    try {
      const track = this.stream.getVideoTracks()[0];
      const caps = track.getCapabilities?.();
      const pick = (val, min, max) => {
        if (min == null && max == null) return val;
        const lo = (typeof min === 'number') ? min : val;
        const hi = (typeof max === 'number') ? max : val;
        return Math.min(Math.max(val, lo), hi);
      };
  
      if (caps) {
        const wantW  = pick(idealW,  caps.width?.min,  caps.width?.max);
        const wantH  = pick(idealH,  caps.height?.min, caps.height?.max);
        const wantAR = idealW / Math.max(1, idealH);
  
        await track.applyConstraints({
          width:       { ideal: wantW },
          height:      { ideal: wantH },
          aspectRatio: { ideal: wantAR },
          frameRate:   { ideal: CONFIG.CAMERA.FRAME_RATE || 30, max: 60 },
          // resizeMode: caps.resizeMode?.includes?.("none") ? "none" : undefined,
        });
      }
    } catch (e) {
      console.warn("applyConstraints failed:", e);
    }
  
    try { await this.videoEl.play(); } catch {}
  }

  // åœæ­¢ç›¸æ©Ÿ
  stopCamera() {
    try {
      if (this.stream) this.stream.getTracks().forEach((t) => t.stop());
    } catch {}
    this.stream = null;
  }

  // åˆ‡æ›å‰å¾Œé¡é ­
  async switchCamera() {
    this.currentFacingMode = this.currentFacingMode === "user" ? "environment" : "user";
    await this.startCamera();
  }

  // å–å¾—ç•¶å‰é¡é ­æ¨¡å¼
  getCurrentFacingMode() {
    return this.currentFacingMode;
  }

  // å–å¾—ä¸²æµ
  getStream() {
    return this.stream;
  }
}