import { CONFIG } from './config.js';

let imageSegmenter = null;
let faceDetector = null;
let wasmFileset = null;

// MediaPipe æ¨¡çµ„è¼‰å…¥
async function ensureTasksVisionLoaded() {
  if (window.FilesetResolver && window.ImageSegmenter) return true;
  const urls = [
    "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@0.10.7/vision_bundle.mjs",
    "https://unpkg.com/@mediapipe/tasks-vision@0.10.7/vision_bundle.mjs",
  ];
  for (const url of urls) {
    try {
      const mod = await import(url);
      if (mod.FilesetResolver) window.FilesetResolver = mod.FilesetResolver;
      if (mod.ImageSegmenter) window.ImageSegmenter = mod.ImageSegmenter;
      if (mod.FaceDetector) window.FaceDetector = mod.FaceDetector;
      return true;
    } catch (e) {
      console.warn("[WARN] failed to import tasks-vision from", url, e);
    }
  }
  throw new Error("ç„¡æ³•è¼‰å…¥ @mediapipe/tasks-vision æ¨¡çµ„");
}

// åˆå§‹åŒ– MediaPipe Tasks
async function initTasks(outCanvas) {
  if (!wasmFileset) {
    wasmFileset = await FilesetResolver.forVisionTasks(CONFIG.WASM_BASE);
  }
  
  // ä¾ outCanvas æ¯”ä¾‹é¸æ“‡ä½¿ç”¨ landscape/square
  try {
    const rect = outCanvas.getBoundingClientRect();
    CONFIG.SEGMENTER.USE_LANDSCAPE_MODEL =
      (rect.width || 1) >= (rect.height || 1);
  } catch {}

  // ImageSegmenter
  const baseOptions = {
    modelAssetPath: CONFIG.SEGMENTER.USE_LANDSCAPE_MODEL
      ? CONFIG.MODELS.SELFIE_SEGMENTER_LANDSCAPE
      : CONFIG.MODELS.SELFIE_SEGMENTER_SQUARE,
    delegate: CONFIG.SEGMENTER.USE_GPU ? "GPU" : "CPU",
  };
  
  try {
    imageSegmenter = await ImageSegmenter.createFromOptions(wasmFileset, {
      baseOptions,
      runningMode: "VIDEO",
      outputCategoryMask: false,
      outputConfidenceMasks: true,
    });
  } catch (e) {
    console.warn("[Segmenter] GPU å¤±æ•—ï¼Œæ”¹ç”¨ CPU:", e);
    imageSegmenter = await ImageSegmenter.createFromOptions(wasmFileset, {
      baseOptions: { ...baseOptions, delegate: "CPU" },
      runningMode: "VIDEO",
      outputCategoryMask: false,
      outputConfidenceMasks: true,
    });
  }

  // FaceDetectorï¼ˆå¯é¸ï¼‰
  try {
    faceDetector = await FaceDetector.createFromOptions(wasmFileset, {
      baseOptions: {
        modelAssetPath: CONFIG.MODELS.FACE_DETECTOR,
        delegate: "GPU",
      },
      runningMode: "VIDEO",
      minDetectionConfidence: 0.6,
      minSuppressionThreshold: 0.3,
    });
  } catch (e) {
    console.warn("[FaceDetector] GPU å¤±æ•—ï¼Œæ”¹ç”¨ CPU:", e);
    faceDetector = await FaceDetector.createFromOptions(wasmFileset, {
      baseOptions: {
        modelAssetPath: CONFIG.MODELS.FACE_DETECTOR,
        delegate: "CPU",
      },
      runningMode: "VIDEO",
      minDetectionConfidence: 0.6,
      minSuppressionThreshold: 0.3,
    });
  }
}

// MediaPipe ç®¡ç†å™¨é¡åˆ¥
export class MediaPipeManager {
  constructor() {
    this.imageSegmenter = null;
    this.faceDetector = null;
    this.wasmFileset = null;
  }

  // åˆå§‹åŒ– MediaPipe
  async init(outCanvas) {
    await ensureTasksVisionLoaded();
    await initTasks(outCanvas);
    this.imageSegmenter = imageSegmenter;
    this.faceDetector = faceDetector;
    this.wasmFileset = wasmFileset;
  }

  // å–å¾— ImageSegmenter
  getImageSegmenter() {
    return this.imageSegmenter;
  }

  // å–å¾— FaceDetector
  getFaceDetector() {
    return this.faceDetector;
  }

  // æª¢æŸ¥æ˜¯å¦å·²åˆå§‹åŒ–
  isInitialized() {
    return this.imageSegmenter !== null && this.faceDetector !== null;
  }

  // åŸ·è¡Œå½±åƒåˆ†å‰²
  async segmentImage(canvas, timestamp) {
    if (!this.imageSegmenter) return null;
    
    try {
      const result = this.imageSegmenter.segmentForVideo
        ? this.imageSegmenter.segmentForVideo(canvas, timestamp)
        : await this.imageSegmenter.segment(canvas);
      return result;
    } catch (e) {
      console.warn("segmentForVideo error:", e);
      return null;
    }
  }

  // åŸ·è¡Œè‡‰éƒ¨æª¢æ¸¬
  detectFaces(canvas, timestamp) {
    if (!this.faceDetector) {
      // Debug: æª¢æŸ¥ faceDetector æ˜¯å¦å­˜åœ¨
      console.warn('âš ï¸ faceDetector not initialized');
      return null;
    }
    
    try {
      const det = this.faceDetector.detectForVideo
        ? this.faceDetector.detectForVideo(canvas, timestamp)
        : this.faceDetector.detect(canvas);
      return det;
    } catch (e) {
      console.warn("faceDetector error:", e);
      return null;
    }
  }
}